/* This is the styling applied to markdown (like the one used for Changelog). */

.gd-markdown-default-theme {
  /* Use the fonts provided by the operating system(s) as possible. */
  /* If you update this font list, be sure to do it in all the other places using fonts in the codebase. */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, 'Helvetica Neue', Helvetica, sans-serif,
    'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
}
.gd-markdown-default-theme.standalone-text-container {
  font-size: 16px;
  line-height: 1.3;
}

.gd-markdown-default-theme h1 {
  margin-bottom: 10px;
}

.gd-markdown-default-theme h2 {
  margin-bottom: 10px;
}

.gd-markdown-default-theme ul {
  word-break: break-word;
}

.gd-markdown-default-theme blockquote {
  border-left: 5px solid rgba(0, 0, 0, 0.6);
  padding-left: 15px;
  margin-left: 20px;
}

.gd-markdown-default-theme a {
  color: rgb(74, 176, 228);
  cursor: pointer;
}

.gd-markdown-default-theme code {
  font-family: "Lucida Console", Monaco, monospace;
  -webkit-user-select: text;
  user-select: text;
  cursor: text;
}

.gd-markdown-default-theme img {
  max-width: 100%;
}
